import { InputStream } from '../Interfaces'
export default class ConsoleInputStream implements InputStream {
    private state: string = 'idle';
    private keyEventHandlers: { [key: string]: { [key: string]: (state: string, key: string) => string } } = {
        'idle': {},
        'reading': {
            'All': (state, key) => {
                this.echo.textContent = this.buffer.join('');
                this.buffer.push(key);
                return 'idle';
            }
        },
        'readingLine': {
            'Enter': (state, key) => {
                return 'idle'
            },
            'Backspace': (state, key) => {
                this.buffer.splice(this.buffer.length - 1, 1);
                this.echo.textContent = this.buffer.join('');
                return state;
            },
            'All': (state, key) => {
                this.buffer.push(key);
                this.echo.textContent = this.buffer.join('');
                return state;
            }
        }
    };
    private stateChangeHandlers: { [key: string]: () => void } = {};
    private buffer: string[] = [];
    private echo: Element;

    constructor(inputContainer: Element) {
        this.echo = document.createElement('pre');
        inputContainer.appendChild(this.echo);

        let input = document.createElement('input');
        inputContainer.appendChild(input);
        document.addEventListener('click', (evt) => {
            // focus when not selecting
            if(document.getSelection().isCollapsed){
                input.focus()
            }
        });

        input.addEventListener('input', (evt: InputEvent) => {
            if (this.isComposing(evt)){
                this.echo.textContent = this.buffer.join('') + input.value;
            }
            else {
                for (let i = 0; i < input.value.length; i++) {
                    this.readKey(input.value[i]);
                }
                this.echo.textContent = this.buffer.join('');
                input.value = ''
            }
        });
        input.addEventListener('keydown', (evt: KeyboardEvent) => {
            switch (evt.key) {
                case 'Enter':
                case 'Backspace':
                    this.readKey(evt.key);
                    break;
            }
        });
    }
    
    private composingText = '';
    private isComposing(evt: InputEvent): boolean {
        let isComposing = evt.isComposing;
        if(isComposing) {
            // Fix chrome composing is always true issue.
            if(this.composingText == evt.srcElement.value){
                isComposing = false;
                this.composingText = '';
            }else{
                this.composingText = evt.srcElement.value;
            }
        }
        return isComposing;
    }
    private readKey(key: string) {
        let entry = this.keyEventHandlers[this.state];
        if (!entry) {
            return;
        }
        let handler = entry[key];
        if (!handler) {
            handler = entry['All'];
        }
        if (!handler) {
            return;
        }
        let stateOld = this.state;
        this.state = handler(this.state, key);
        let stateNew = this.state;
        if (stateOld != stateNew) {
            let handler = this.stateChangeHandlers[`${stateOld}-${stateNew}`];
            if (handler) {
                handler();
            }
        }
    }

    read() {
        return new Promise<string>((resolve, reject) => {
            this.state = 'reading';
            this.buffer = [];
            this.stateChangeHandlers['reading-idle'] = () => {
                resolve(this.buffer.join(''));
                this.buffer = [];
                this.stateChangeHandlers['reading-idle'] = undefined;
            }
        });
    }

    readLine() {
        return new Promise<string>((resolve, reject) => {
            this.state = 'readingLine';
            this.buffer = [];
            this.stateChangeHandlers['readingLine-idle'] = () => {
                resolve(this.buffer.join(''));
                this.buffer = [];
                this.echo.textContent = this.buffer.join('');
                this.stateChangeHandlers['readingLine-idle'] = undefined;
            }
        });
    }
}

interface InputEvent extends Event {
    isComposing: boolean,
    srcElement: HTMLInputElement
}



