export function get(url: string, query: any): Promise<any> {
    let http = new XMLHttpRequest();
    return new Promise<string>((resolve, reject) => {
        http.onreadystatechange = (evt) => {
            if (http.readyState == 4 && http.status == 200) {
                resolve(JSON.parse(http.responseText))
            }
        };
        http.open("GET", `${url}?${serializeQuery(query)}`, true);
        http.send();
    });
}

export function serializeQuery(query: { [key: string]: any }): string {
    let queries = [];
    for (const key in query) {
        if (query.hasOwnProperty(key)) {
            queries.push(`${encodeURIComponent(key)}=${encodeURIComponent(query[key])}`)
        }
    }
    return queries.join('&');
}


